import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AppService } from '../services/app.service';



@Component({
  selector: 'app-single',
  templateUrl: './single.page.html',
  styleUrls: ['./single.page.scss'],
})
export class SinglePage implements OnInit {

  constructor(private _AppService:AppService, private _ActivatedRoute:ActivatedRoute) { }

  id

  _url

  _removed_rn

  title
  img
  text
  _after
  date
  author

  data_loaded = false

  _trends

  article_images
  
  recieve_data(){
    this.title = localStorage.getItem('title')
    this.img = localStorage.getItem('img')
    this.text = localStorage.getItem('text')
    this.date = localStorage.getItem('date')
    this.author = localStorage.getItem('author')
    // localStorage.clear();
    this._removed_rn = this.text.replace(/(?:\r\n|\r|\n)/g, '<br>');
    this._after = this._removed_rn.replace(/&quot;/g,' ')
    // console.log(this.text)
    // console.log(_after)
  }

  back(){
    this._AppService.go_back()
    console.log('back')
  }

  related_news(){
    this.data_loaded = false
    this._AppService.get_data(`app_news_by_sections/58`,'r').subscribe((res:any)=>{
      console.log(res)
      this.data_loaded = true
      this._trends = res.results.section_58
    })
  }

  get_images(){
    this._AppService.get_data(`app_images_by_news_id/${this.id}`,'r').subscribe((res:any)=>{
      console.log(res)
      if(res != null){
        this.article_images = res.results
      }
    })
  }

  ngOnInit() {
    this.id = this._ActivatedRoute.snapshot.params['id'];
    this._url = `https://wa.me/?text=https://arriyadiyah.com/${this.id}`
    this.get_images()
    
    this.recieve_data()
    this.related_news()

  }

  // whatsapp_share(){
  //   window.open(`whatsapp://send?text=${this.title}-https://arriyadiyah.com/${this.id}`,"_blank")
  // }

  



}
